@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;700;900&display=swap);
.userlist__heading{
    margin: 3rem 0;
    text-transform: uppercase;
    font-weight: 900;
    font-size: 2.6rem;
}
.userlist__name{
    font-weight: 700;
    font-size: 1.6rem;
    text-transform: uppercase;
    padding-bottom: 1rem;
}

.userlist__name:not(:last-child){
    border-bottom: 1px solid var(--grey);
}
.chat__pane{
    margin: 2rem 0;
    height: 68vh;
    overflow-y: scroll;
    display: block;
}

.chat__description{
    font-size: 2rem;
    line-height: 3rem;
    list-style: none;
}

.chat__border{
    width: 50%;
    margin:auto;
}

.usernameBlue{
    color:blue;
    text-transform: uppercase;
}

.usernameRed{
    color:var(--red);
    text-transform: uppercase;
}


@-webkit-keyframes dot1 {
    0%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
}


@keyframes dot1 {
    0%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
}

.chat__dot1,.chat__dot2,.chat__dot3{
    font-size: 3rem;
    margin:0 3px;
}

.chat__dot1{
    -webkit-animation: dot1 .6s linear infinite;
            animation: dot1 .6s linear infinite;
}

.chat__dot2{
    -webkit-animation: dot1 .6s linear infinite;
            animation: dot1 .6s linear infinite;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
    -webkit-animation-delay: .1s;
            animation-delay: .1s;

    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
}

.chat__dot3{
    -webkit-animation: dot1 .6s linear infinite;
            animation: dot1 .6s linear infinite;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}

@media only screen and (max-width: 500px) {
    .chat__pane{
        height: 55vh;
        width: 100vw;
        padding: 1rem;
    }
}
.form{
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    z-index: 111111;
    
}

.form__input{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    border-bottom: 5px solid var(--black);
    margin-bottom: 3rem;
    width: 50vw;
    border-radius: 3px;
}

.form__input:focus{
    outline: none;
}

.form__button,
.form__button:link,
.form__button:visited{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    text-transform: uppercase;
    width: 50vw;
    cursor: pointer;
    border-radius: 3px;
    background-image: linear-gradient(105deg, var(--blue-primary) 50%, var(--blue-secondary)50%);
    background-size: 220%;
    transition: all 1s;
    color:var(--white);
}

.form__button:hover{
    background-position: 100%;
    color:var(--white);
}

.form__button:disabled{
    color:var(--white);
    cursor: no-drop;
    opacity: .3;
    background-position: 0;
}

@media only screen and (max-width: 500px) {
    .form__button,.form__input{
        width: 90vw;
    }
}
.chat{
    height:85vh;
    display: flex;
    justify-content: space-evenly;
}
.chat__heading{
    font-size: 3rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 3rem 0;
}

.chat__secondary{
    font-size: 1.6rem;
    text-transform: uppercase;
    font-weight: 700;
}

.chat__typing{
    font-size: 1.6rem;
    color: red;
    font-weight: 700;
    text-transform: uppercase;
}
@media only screen and (max-width: 500px) {
    .chat__button, .chat__input,.chat__border{
        width: 95%;
    }
}
.auth{
    background-image: linear-gradient(120deg, var(--red) 50%, var(--black) 50%);
    height: 100vh;
    position: relative;
}
:root{
    --black: rgb(0,0,0);
    --red: rgb(255,0,0);
    --white: rgb(255,255,255);
    --grey: rgb(241, 239, 239);
    --blue-primary: rgb(109, 185, 242);
    --blue-secondary: rgb(0, 104, 183);
}
*{
    padding:0;
    margin: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
}

body{
    font-family: 'Roboto', sans-serif;
    line-height: 1.6rem;
}

.spinner{
    height:5rem;
    width: 5rem;
    border-radius: 50%;
    border: 3px solid rgb(0,0,0);
    border: 3px solid var(--black);
    border-bottom: 3px solid transparent;
    -webkit-animation: spin 1s ease-out infinite;
            animation: spin 1s ease-out infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);

}


@-webkit-keyframes spin {
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


@keyframes spin {
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

