.chat__pane{
    margin: 2rem 0;
    height: 68vh;
    overflow-y: scroll;
    display: block;
}

.chat__description{
    font-size: 2rem;
    line-height: 3rem;
    list-style: none;
}

.chat__border{
    width: 50%;
    margin:auto;
}

.usernameBlue{
    color:blue;
    text-transform: uppercase;
}

.usernameRed{
    color:var(--red);
    text-transform: uppercase;
}


@keyframes dot1 {
    0%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
}

.chat__dot1,.chat__dot2,.chat__dot3{
    font-size: 3rem;
    margin:0 3px;
}

.chat__dot1{
    animation: dot1 .6s linear infinite;
}

.chat__dot2{
    animation: dot1 .6s linear infinite;
    animation-fill-mode: backwards;
    animation-delay: .1s;

    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
}

.chat__dot3{
    animation: dot1 .6s linear infinite;
    animation-fill-mode: backwards;
    animation-delay: .2s;
}

@media only screen and (max-width: 500px) {
    .chat__pane{
        height: 55vh;
        width: 100vw;
        padding: 1rem;
    }
}