.form{
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 111111;
    
}

.form__input{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    border-bottom: 5px solid var(--black);
    margin-bottom: 3rem;
    width: 50vw;
    border-radius: 3px;
}

.form__input:focus{
    outline: none;
}

.form__button,
.form__button:link,
.form__button:visited{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    text-transform: uppercase;
    width: 50vw;
    cursor: pointer;
    border-radius: 3px;
    background-image: linear-gradient(105deg, var(--blue-primary) 50%, var(--blue-secondary)50%);
    background-size: 220%;
    transition: all 1s;
    color:var(--white);
}

.form__button:hover{
    background-position: 100%;
    color:var(--white);
}

.form__button:disabled{
    color:var(--white);
    cursor: no-drop;
    opacity: .3;
    background-position: 0;
}

@media only screen and (max-width: 500px) {
    .form__button,.form__input{
        width: 90vw;
    }
}