.chat{
    height:85vh;
    display: flex;
    justify-content: space-evenly;
}
.chat__heading{
    font-size: 3rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 3rem 0;
}

.chat__secondary{
    font-size: 1.6rem;
    text-transform: uppercase;
    font-weight: 700;
}

.chat__typing{
    font-size: 1.6rem;
    color: red;
    font-weight: 700;
    text-transform: uppercase;
}
@media only screen and (max-width: 500px) {
    .chat__button, .chat__input,.chat__border{
        width: 95%;
    }
}